

















export default {
	data() {
		return {
			news:[]
		}
	},
	onLoad:function(){
		uni.showLoading({
			title:"加载中....."
		})
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				this.news=res.data;//将data数据里的数据填充到news里
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openInfo(e) {
			var newsid = e.currentTarget.dataset.newsid
			uni.navigateTo({
				url: '../info/info?newsid='+newsid//传递参数
			});
		}
	},

}
